import React, { PureComponent } from 'react'

import Icon from '@material-ui/core/Icon'
import Typography from '@material-ui/core/Typography'
import Breadcrumbs from '@material-ui/core/Breadcrumbs'
import Link from '@material-ui/core/Link'
import {
  ClearBreadcrumbs,
  UpdateBreadcrumbs,
  UpdateEntity,
  SetActiveData
} from '../../redux/actions'

const ContainerWrapper = function(EnhancedComponent) {
  return class extends PureComponent {
    constructor(props) {
      super(props)
    }

    componentDidMount() {
      const { route, dispatch } = this.props
      document.title = route.label || 'Agile Wave Template'
      dispatch(ClearBreadcrumbs())
      dispatch(UpdateBreadcrumbs(route))
      dispatch(SetActiveData(route))
    }

    handleBreadCrumbClick = (data) => {
      const { path, type, entity = '' } = data
      const { history, dispatch, breadCrumbs } = this.props
      let newBreadCrumbs = [...breadCrumbs]
      if (path) {
        // Update breadcrumbs to the current index clicked
        const currentIndex = newBreadCrumbs.findIndex(e => e.path === path) + 1
        newBreadCrumbs.splice(currentIndex, newBreadCrumbs.length)

        dispatch(UpdateBreadcrumbs(newBreadCrumbs))
        dispatch(UpdateEntity(entity))
        dispatch(SetActiveData(data))

        if (type === 'parent'){
          history.push(path)
        }
      }
    }

    render() {
      const { breadCrumbs = [], activeData } = this.props
      const activeLabel = (breadCrumbs[breadCrumbs.length - 1] || {}).label || ''
      const breadCrumbsLength = breadCrumbs.length > 1
      
      return (
        <>
          <Breadcrumbs aria-label='breadcrumb' className={breadCrumbsLength ? '' : 'homeIcon'}>
            <Icon>home</Icon>
            {
              breadCrumbs.map((e, i) => {
                const { label } = e
                return (i < breadCrumbs.length - 1)
                  ? (<Link
                      key={new Date().toISOString() + i}
                      color='inherit'
                      onClick={() => this.handleBreadCrumbClick(e)}
                    >
                      {label}
                    </Link>)
                  : <Typography
                      key={new Date().toISOString() + i}
                      color='textPrimary'
                    >
                      {label}
                    </Typography>
              })
            }
          </Breadcrumbs>
          <Typography variant='h4' gutterBottom>
            {activeLabel}
          </Typography>
          <EnhancedComponent
            key={activeData.path}
            {...this.props}
          />
        </>
      )
    }
  }
}

export default ContainerWrapper